.calculator-page {
    color: #eeeff0;
    &__total {
        font-size: 24px;
        font-weight: bold;
        text-transform: uppercase;
    }
    &__data {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        margin-bottom: 10px;
    }
    &__buttons {
        & > button {
            margin-left: 20px;
        }
    }
    &__need-content {
        position: absolute;
        border: solid 1px #f6e1c2;
        background: #1d1f2a;
        padding: 10px;
    }
    &__need {
        text-transform: uppercase;
    }
    &__need-table {
        td {
            width: inherit !important;
        }
    }
    table {

        text-align: left;
        td, th {
            box-sizing: border-box;
            width: 100px;
            font-weight: normal;
            padding: 2px;
        }
        th {
            text-transform: uppercase;
            font-size: 14px;
        }
    }
    input {
        color: #7398e4;
        background: #383c49;
        border: none;
        border-radius: 0;
        padding: 5px 10px;
        width: 80px;
    }
    button {
        background: #ddd;
        border-radius: 0;
        border: none;
        color: #333;
        text-transform: uppercase;
        padding: 7px 50px;
        cursor: pointer;
        &.button_save {
            background: green;
            color: #fff;
        }
        &.button_load {
            background: #3471ee;
            color: #fff;
        }
    }

    &__field {
        display: grid;
        padding: 20px;
        border: 1px solid #f6e1c2;
        background: url('./assets/grass.jpeg') repeat;

        input {
            background: rgb(28,30,37);
            color: #fff;
            //border-bottom: 1px solid #fff;
            text-align: center;
            outline: none;
        }
    }

    &__boost {
        position: relative;
        input {
            width: 24px;
            font-size: 16px;
            padding: 0 5px;
            color: rgb(179,259, 53);
        }
    }

    &__ovr {
        top: 20px;
        position: relative;
        input {
            width: 40px;
            font-size: 30px;
            font-weight: bold;
            padding: 0 5px;
        }
    }

    &__rank {
        position: relative;
        top: 60px;
        input {
            width: 24px;
            font-size: 14px;
            padding: 0 5px;
        }
    }

    &__position {
        width: 128px;
        height: 128px;
        background-image: url('./assets/card.png');
        background-size: 128px;
        text-align: center;
        &_master {
            background-image: url('./assets/card_master.png');
        }
        &_position_1 {
            grid-row: 1;
            grid-column: 2;
        }
        &_position_2 {
            grid-row: 1;
            grid-column: 4;
        }
        &_position_3 {
            grid-row: 2;
            grid-column: 3;
        }
        &_position_4 {
            grid-row: 3;
            grid-column: 1;
            top: -64px;
            left: 64px;
            position: relative;
        }
        &_position_5 {
            grid-row: 3;
            grid-column: 5;
            top: -64px;
            left: -64px;
            position: relative;
        }
        &_position_6 {
            grid-row: 4;
            grid-column: 3;
            top: -64px;
            position: relative;
        }
        &_position_7 {
            grid-row: 5;
            grid-column: 1;
            top: -32px;
            left: 64px;
            position: relative;
        }
        &_position_8 {
            grid-row: 5;
            grid-column: 2;
            top: -32px;
            left: 64px;
            position: relative;
        }
        &_position_9 {
            grid-row: 5;
            grid-column: 4;
            top: -32px;
            left: -64px;
            position: relative;
        }

        &_position_10 {
            grid-row: 5;
            grid-column: 5;
            top: -32px;
            left: -64px;
            position: relative;
        }

        &_position_11 {
            grid-row: 6;
            grid-column: 3;
        }
    }
}
